.card {
	position: relative;
	width: 303px;
	height: 419px;
	background-color: rgb(255, 255, 255, 0.1);
	border-radius: 7px;
	margin: 0px 15px 15px 0px;
	transition: all 0.5s;
	display: block;
	box-shadow: 1px 1px 5px rgb(0, 0, 0);
	color: #f1f1f1;
	overflow: hidden;
}
.mtb {
	background-color: rgba(93, 32, 32, 0.6);
}

.card:hover {
	transform: translateY(-5px);
	box-shadow: 8px 8px 10px rgb(0, 0, 0);
	transition: all 0.5s;
}

.title {
	font-family: 'Montserrat Alternates';
	font-style: normal;
	font-weight: bold;
	font-size: 24px;
	line-height: 25px;
	letter-spacing: -0.5px;
	margin: 15px 15px 0px 15px;
}

.title_sub {
	font-size: 16px;
	margin: 0px 15px 15px 15px;
	color: rgba(225, 254, 255, 0.45);
}

.names {
	font-weight: 500;
	margin-left: 5px;
}

.names_data {
	color: rgba(225, 254, 255, 0.45);
	margin-left: 5px;
}

.text {
	font-family: 'Montserrat Alternates';
	font-style: normal;
	font-weight: normal;
	font-size: 14px;
	line-height: 14px;
	/* letter-spacing: 0.08em; */
	margin: 15px;
}

.description {
	margin-bottom: 10px;
}

.img {
	width: 303px;
	height: 184px;
	border-radius: 7px 7px 0px 0px;
}

.card__frame_kudos {
	position: absolute;
	width: 30px;
	height: 70px;
	top: 333px;
	right: 15px;
	text-align: center;
	padding-top: 4px;
	border: solid 1px #f1f1f1;
	border-radius: 4px;
}
.frame_kudos__kudos {
	padding-bottom: 2px;
}
.frame_kudos__number_text {
	font-family: 'Montserrat Alternates';
	font-size: 12px;
	color: rgba(225, 254, 255, 0.45);
}
.frame_kudos__eye {
	padding-top: 4px;
}

@media screen and (max-width: 510px) {
	.card {
		margin: 0px 0px 15px 0px;
	}
}
